import { AnimatePresence, motion } from "framer-motion";
import { useState } from "react";
import { Link } from "react-router-dom";

const Dropdown = ({
  children,
  url,
  content,
  marker = false,
  separate = false,
  animation = "underline",
}) => {
  const [open, setOpen] = useState(false);
  const showDropdown = content && open;

  return (
    <div
      onMouseEnter={() => setOpen(true)}
      onMouseLeave={() => setOpen(false)}
      className="h-full flex items-center justify-center"
      style={{ position: separate ? "relative" : "" }}
    >
      <Link
        to={url}
        className="relative h-full flex justify-center items-center"
        style={
          showDropdown && animation === "highlight" ? { color: "#c1272d" } : {}
        }
      >
        {children}

        {animation === "underline" && (
          <span
            style={{
              transform: open ? "scaleX(1)" : "scaleX(0)",
            }}
            className="absolute bottom-0 -left-2 -right-2 h-1 origin-left scale-x-0 rounded-full bg-primeRed transition-transform duration-300 ease-out"
          />
        )}
      </Link>
      <AnimatePresence>
        {showDropdown && (
          <motion.div
            initial={{ opacity: 0, y: 15 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: 15 }}
            style={{ translateX: "-50%", top: "103%" }}
            transition={{ duration: 0.3, ease: "easeOut" }}
            className="absolute left-1/2 z-10"
          >
            {marker && (
              <div className="absolute left-1/2 top-0 h-4 w-4 -translate-x-1/2 -translate-y-1/2 rotate-45 bg-white" />
            )}
            {content}
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default Dropdown;
