import Dropdown from "../../components/Dropdown";
import useCategories from "../../hooks/useCategories";
import useMediaQuery from "../../hooks/useMediaQuery";
import SubCategoryNav from "./SubCategoryNav";

const CategoryNav = () => {
  const { categories, loading } = useCategories();
  const maxLg = useMediaQuery("(min-width: 1024px)");

  return (
    maxLg && (
      <section className="border-b border-secondary w-full">
        <div className="container max-w-full">
          <ul className="relative flex gap-8 w-max m-auto">
            {loading ? (
              <div className="h-12" />
            ) : (
              categories.map((category, index) => (
                <li
                  key={index}
                  className="h-12 capitalize font-medium text-shadow-lg"
                >
                  <Dropdown
                    url={`/category/${category._id}`}
                    content={
                      <SubCategoryNav subCategories={category.subCategories} />
                    }
                  >
                    {category.name}
                  </Dropdown>
                </li>
              ))
            )}
          </ul>
        </div>
      </section>
    )
  );
};

export default CategoryNav;
