import axiosInstance from "./axiosInstance";

const getAllCategoryDetails = () => {
  return axiosInstance.get("/allCategoryDetails");
};

const getAllCategories = () => {
  return axiosInstance.get("/getAllCategory");
};

const getAllFeaturedCategories = () => {
  return axiosInstance.get("/getAllFeaturedCategory");
};

const getCategory = (categoryId) => {
  return axiosInstance.get(`/getCategoryId/${categoryId}`);
};

const getAllSubcategories = () => {
  return axiosInstance.get("/getAllSubCategory");
};

const getAllFeaturedSubCategories = () => {
  return axiosInstance.get("/getAllFeaturedSubCategory");
};

const getSubcategories = (categoryId) => {
  return axiosInstance.get(`/getSubcategoryBasedOnCategory/${categoryId}`);
};

const getSubcategory = (subcategoryId) => {
  return axiosInstance.get(`/getSubCategoryId/${subcategoryId}`);
};

const getAllProductTypes = () => {
  return axiosInstance.get("/getAllProductType");
};

const getProductTypes = (subcategoryId) => {
  return axiosInstance.get(
    `/getProductTypeBasedOnSubCategory/${subcategoryId}`
  );
};

const getAllFeaturedProductTypes = async () => {
  const response = await axiosInstance.get("/getAllFeaturedProductType");
  return response.data;
};

const getProductType = (productTypeId) => {
  return axiosInstance.get(`/getProductTypeById/${productTypeId}`);
};

const getProductTypesBasedOnBrand = async (brandId) => {
  const response = await axiosInstance.get(
    `/getProductTypesBasedOnBrand/${brandId}`
  );
  return response.data?.ProductTypeList;
};

export {
  getAllCategoryDetails,
  getAllCategories,
  getAllFeaturedCategories,
  getCategory,
  getAllSubcategories,
  getAllFeaturedSubCategories,
  getSubcategories,
  getSubcategory,
  getAllProductTypes,
  getProductTypes,
  getAllFeaturedProductTypes,
  getProductType,
  getProductTypesBasedOnBrand,
};
