import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";

const SubCategoryNav = ({ subCategories }) => {
  const containerRef = useRef(null);
  const [numOfCols, setNumOfCols] = useState(0);

  const calcNumOfCols = () => {
    if (!containerRef.current) return;
    setNumOfCols(Math.floor(containerRef.current?.offsetWidth / 160));
  };

  useEffect(() => {
    calcNumOfCols();
  }, []);

  return (
    subCategories.length > 0 && (
      <div
        className="relative flex min-w-[960px] h-96 bg-white shadow-drop"
        style={{ textShadow: "none" }}
      >
        <div className="absolute inset-0 flex">
          {[...Array(numOfCols).keys()].map((_, index) => (
            <div key={index} className="w-40 h-full even:bg-lightGray"></div>
          ))}
        </div>
        <div ref={containerRef} className="flex flex-col flex-wrap z-10">
          {subCategories.map((subCategory, index) => (
            <div key={index} className="w-40 p-2 space-y-1">
              <Link
                to={`/subcategory/${subCategory._id}`}
                className="SUB-HEADING"
              >
                <h4 className="text-sm font-medium text-mainPink">
                  {subCategory.name}
                </h4>
              </Link>

              <ul>
                {subCategory.productTypes?.map((item, index) => (
                  <li
                    key={index}
                    className="ITEM text-sm text-primary hover:text-lavender transition-colors duration-150 ease-in-out font-normal"
                  >
                    <Link to={`/product-type/${item._id}`}>{item.name}</Link>
                  </li>
                ))}
              </ul>
            </div>
          ))}
        </div>
      </div>
    )
  );
};

export default SubCategoryNav;
